@mixin article--is-it-web-or-photoshop {
  margin-right: 0;

  // to hide the avatars from What some people ...
  position: relative;
  z-index: 10;

  .article__slider {
    width: 50%;
  }

  // The slider has images
  .slider {
    @include slider--with-image;
    @include intrinsic-ratio(3, 4, '.slider__slides');
  }
}
